import React, { useState, useEffect } from "react";
import "./Homepage.css";
import LargeCarousal from "../LargeCarousal/LargeCarousal";
import SmallCarousal from "../SmallCarousal/SmallCarousal";
import Navbar from "../navbar/navbar";
import axios from "axios";
import Cart from "../Cart/cart";

const Homepage = (props) => {
  const [products, setProducts] = useState([]);
  const [FinalCart, setFinalCart] = useState([]);

  useEffect(() => {
    console.log(FinalCart);
    axios
      .get("http://localhost:8000/api/products", {
        headers: {},
      })
      .then((res) => {
        setProducts(res.data);
      })
      .catch((err) => {
        console.log("Error from Airport Api");
      });
  }, [FinalCart]);

  const FinalCartHandler = (cart) => {
    setFinalCart(cart);
  };
  console.log("Final Cart", FinalCart);
  return (
    <div>
      <Navbar></Navbar>
      <div className="Homepage">
        <h1 className="h12">Winter Exhibition 2024</h1>
        <LargeCarousal></LargeCarousal>
        <div className="OurStory">
          <h1 className="h15">Our Story</h1>
          <p>
            From comfortable and casual streetwear to sophisticated and elegant
            wear, our collection line has something for everyone. Our designers
            have carefully crafted each piece to ensure that it not only looks
            great but also feels comfortable and practical to wear.
          </p>
        </div>
        <h1 className="h12">Our Products</h1>
        <SmallCarousal
          products={products}
          FinalCartHandler={FinalCartHandler}
        ></SmallCarousal>
        <hr></hr>
        {FinalCart.length > 0 && (
          <div>
            <h1 className="h12">Cart</h1>
            <Cart FinalCart={FinalCart} products={products}></Cart>
          </div>
        )}
      </div>
    </div>
  );
};

export default Homepage;
