import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import ProductCard from "../ProductCard/ProductCard";
import "@coreui/coreui/dist/css/coreui.min.css";
import React, { useState, useEffect } from "react";

import { CCarousel, CCarouselItem } from "@coreui/react";
const SmallCarousal = (props) => {
  const [SwipeEnabled, setSwipeEnabled] = useState(true);
  const [cart, setCart] = useState([]);

  const AddToLargeCart = (miniCart) => {
    setCart([...cart, ...miniCart]);
  };
  useEffect(() => {
    console.log("Middle Cart ", cart);
    props.FinalCartHandler(cart);
  }, [cart]);

  return (
    <CCarousel
      indicators
      controllers
      interval={false}
      touch={SwipeEnabled}
      className="ControllingCarousel"
    >
      {props.products.map((product, index) => (
        <CCarouselItem key={index}>
          <ProductCard
            setSwipeEnabled={setSwipeEnabled}
            product={product}
            AddToLargeCart={AddToLargeCart}
          ></ProductCard>
        </CCarouselItem>
      ))}
    </CCarousel>
  );
};
export default SmallCarousal;
