import React, { useState } from "react";
import "./Confirmation.css";
import axios from "axios";
import Logo from "../navbar/logo2.jpg";
import { Link } from "react-router-dom";
import { useLocation } from "react-router-dom";

const Confirmation = () => {
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [showPopup, setShowPopup] = useState(false);
  const [finalScreen, setFinalScreen] = useState(false);
  const location = useLocation();
  const FinalCart = location.state;

  const handleSubmit = (e) => {
    e.preventDefault();
    setShowPopup(true);
  };
  const BackHandler = (e) => {
    e.preventDefault();
    setShowPopup(false);
  };
  const handleConfirm = (e) => {
    e.preventDefault();
    setFinalScreen(true);
    const inputs = {
      firstName: firstName,
      lastName: lastName,
      email: email,
      phoneNumber: phoneNumber,
      cart: FinalCart,
    };
    console.log(inputs);
    axios
      .post("http://localhost:8000/api/orders/addOrder", inputs, {})
      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        console.log("Cannot Add Order");
      });
  };
  return (
    <div className="body2">
      <div className="container">
        <img
          src={Logo}
          style={{
            width: "15%",
            marginLeft: "42.5%",
            marginRight: "42.5%",
          }}
        ></img>
        {!showPopup && !finalScreen && (
          <form onSubmit={handleSubmit}>
            <div className="text">CONFIRMATION FORM</div>
            <div className="form-row">
              <div className="input-data">
                <input
                  type="text"
                  value={firstName}
                  required
                  onChange={(e) => setFirstName(e.target.value)}
                ></input>
                <div className="underline"></div>
                <label>First Name</label>
              </div>
              <div className="input-data">
                <input
                  type="text"
                  required
                  value={lastName}
                  onChange={(e) => setLastName(e.target.value)}
                ></input>
                <div className="underline"></div>
                <label>Last Name</label>
              </div>
            </div>
            <div className="form-row">
              <div className="input-data">
                <input
                  type="email"
                  required
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                ></input>
                <div className="underline"></div>
                <label>Email Address</label>
              </div>
              <div className="input-data">
                <input
                  type="text"
                  required
                  value={phoneNumber}
                  onChange={(e) => setPhoneNumber(e.target.value)}
                ></input>
                <div className="underline"></div>
                <label>Phone Number</label>
              </div>
            </div>
            <button className="SubmitForm" type="submit">
              Submit
            </button>
          </form>
        )}
        {showPopup && !finalScreen && (
          <form onSubmit={handleConfirm}>
            <div class="text">DATA CONFIRMATION</div>
            <div class="form-row">
              <div class="input-data">
                <label>First Name: {firstName}</label>
              </div>
              <div class="input-data">
                <label> Last Name: {lastName}</label>
              </div>
            </div>
            <div class="form-row">
              <div class="input-data">
                <label>Email Address: {email}</label>
              </div>
              <div class="input-data">
                <label>Phone Number: {phoneNumber}</label>
              </div>
            </div>
            <button className="BackButton" onClick={BackHandler}>
              Back
            </button>
            <button className="SubmitForm" type="submit">
              Confirm
            </button>
          </form>
        )}
        {finalScreen && (
          <form>
            <div class="text">THANK YOU</div>
            <div class="text">YOUR DATA HAS BEEN RECEIVED</div>
            <Link to="/">
              <button className="StartAgain">Start Again</button>
            </Link>
          </form>
        )}
      </div>
    </div>
  );
};

export default Confirmation;
