import React from "react";
import "./cart.css"; // Import the CSS file for styling
import { Link } from "react-router-dom";

const Cart = (props) => {
  return (
    <div>
      <div className="cart-container">
        {props.FinalCart.map((product) => (
          <div key={product.productID} className="cart-item">
            <img
              src={require(`../ProductCard/${product.productID}-1.jpg`)}
              alt={product.name}
              className="product-image"
            />
            <div className="product-info">
              <h2>{product.productID}</h2>
              <h5>Colours:</h5>
              <p>{product.selectedColors.join(" ")}</p>
              <h5>Quantity:</h5>
              {Object.entries(product.quantities).map(([key, value]) => (
                <p key={key}>{`${key} - ${value}`}</p>
              ))}
              {/*<button className="remove-button">Remove</button>*/}
            </div>
          </div>
        ))}
      </div>
      <Link to="/Confirmation" state={props.FinalCart}>
        <button className="ConfirmCart">Confirm Cart</button>
      </Link>
    </div>
  );
};

export default Cart;
