import BestSeller from "./Best Sellers2.png";
import SummerCollection from "./SummerCollection.jpg";
import "@coreui/coreui/dist/css/coreui.min.css";
import React from "react";

import { CCarousel, CCarouselItem, CImage } from "@coreui/react";

const LargeCarousal = () => {
  return (
    <div>
      <CCarousel controls indicators ride={true}>
        <CCarouselItem>
          <CImage className="d-block w-100" src={BestSeller} alt="Banner 1" />
        </CCarouselItem>
        <CCarouselItem>
          <CImage
            className="d-block w-100"
            src={SummerCollection}
            alt="Banner 2"
          />
        </CCarouselItem>
      </CCarousel>
    </div>
  );
};
export default LargeCarousal;
