import "./AddToCart.css";
import "../ProductCard/ProductCard.css";
import { useEffect, useState } from "react";

const AddToCart = (props) => {
  const [selectedColors, setSelectedColors] = useState([]);
  const [selectedSizes, setSelectedSizes] = useState([]);
  const [quantities, setQuantities] = useState({});
  const [cartReady, setcartReady] = useState(false);
  useEffect(() => {
    console.log(selectedColors, selectedSizes, quantities);
    if (selectedColors.length === 0) {
      setcartReady(false);
    }
    if (Object.keys(quantities).length === 0) {
      setcartReady(false);
    }
    if (selectedColors.length > 0 && Object.keys(quantities).length > 0) {
      setcartReady(true);
    }
  }, [quantities, selectedColors, selectedSizes]);

  const handleColorChange = (color) => {
    if (selectedColors.includes(color)) {
      setSelectedColors(selectedColors.filter((c) => c !== color));
    } else {
      setSelectedColors([...selectedColors, color]);
    }
  };

  const handleSizeChange = (size, quantity) => {
    setSelectedSizes((prevSizes) => {
      const updatedSizes = { ...prevSizes };
      updatedSizes[size] = quantity;
      return updatedSizes;
    });
  };

  const handleQuantityChange = (size, quantity) => {
    console.log(size, quantity.length);
    if (quantity.length === 0) {
      const updatedquantities = { ...quantities };
      delete updatedquantities[size];
      console.log("Updated", updatedquantities);
      setQuantities(updatedquantities);
    } else {
      setQuantities((prevQuantities) => {
        const updatedQuantities = { ...prevQuantities };
        updatedQuantities[size] = quantity;
        return updatedQuantities;
      });
    }
  };

  const handleAddToCart = () => {
    props.AddedToCart(props.productID, selectedColors, quantities);
  };

  const BackToShoppingHandler = () => {
    props.BackToShopping();
  };

  return (
    <div className="ProductInformation2">
      <link
        rel="stylesheet"
        href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"
      />
      <div>
        <h2>Choose Colours:</h2>
        <div>
          {props.availableColours.map((color) => (
            <label key={color}>
              <div>{color}</div>
              <label className="label">
                <input
                  className="label__checkbox"
                  type="checkbox"
                  checked={selectedColors.includes(color)}
                  onChange={() => handleColorChange(color)}
                />
                <span className="label__text">
                  <span className="label__check">
                    <i className="fa fa-check icon"></i>
                  </span>
                </span>
              </label>
            </label>
          ))}
        </div>
        <br></br>

        <h2>Choose Sizes and Quantities:</h2>
        <div>
          {props.availableSizes.map((size) => (
            <div key={size}>
              <label for="inp" class="inp">
                <span class="label">{size}</span>
                <br></br>
                <input
                  type="text"
                  id="inp"
                  value={quantities[size] || ""}
                  onChange={(e) => handleQuantityChange(size, e.target.value)}
                />
                <span class="focus-bg"></span>
              </label>
            </div>
          ))}
        </div>
        <br></br>
        <button
          disabled={!cartReady}
          onClick={handleAddToCart}
          className="SaveToCart"
        >
          Add to Cart
        </button>
      </div>
    </div>
  );
};
export default AddToCart;
