import Navbar from "../navbar/navbar";
import React, { useState } from "react";
import "./AddProduct.css";
import Logo from "./logo.webp";
import axios from "axios";

const AddProduct = () => {
  const [name, setName] = useState("");
  const [id, setID] = useState("");
  const [order, setOrder] = useState();
  const [material, setMaterial] = useState("");
  const [wholesalePrice, setwholesalePrice] = useState();
  const [consumerPrice, setconsumerPrice] = useState();
  const [MOQs, setMOQs] = useState();
  const [availableColours, setAvailableColours] = useState("");
  const [availableSizes, setAvailableSizes] = useState("");

  const [productAdded, setProductAdded] = useState(false);
  const handleSubmit = (e) => {
    e.preventDefault();
    const inputs = {
      productName: name,
      productID: id,
      productOrderToShow: order,
      productMaterial: material,
      productWholesalePrice: wholesalePrice,
      productConsumerPrice: consumerPrice,
      productMOQs: MOQs,
      coloursAvailable: availableColours.split(/[.-\s]+/),
      sizesAvailable: availableSizes.split(/[.-\s]+/),
    };
    console.log(inputs);
    axios
      .post("http://localhost:8000/api/products/addProduct", inputs, {})
      .then((res) => {
        console.log(res);
        setProductAdded(true);
      })
      .catch((err) => {
        console.log("Error from ShowuserList");
      });
  };
  return (
    <div>
      <div className="form-page">
        <form className="form" onSubmit={handleSubmit}>
          <h2>Enter Product's Details</h2>
          <div className="form-group">
            <label htmlFor="name">Product Name:</label>
            <input
              type="text"
              id="name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="id">Product ID:</label>
            <input
              type="text"
              id="id"
              value={id}
              onChange={(e) => setID(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="order">Order in display:</label>
            <input
              type="text"
              id="order"
              value={order}
              onChange={(e) => setOrder(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="material">Material:</label>
            <input
              type="text"
              id="material"
              value={material}
              onChange={(e) => setMaterial(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="wholesalePrice">Wholesale Price:</label>
            <input
              type="text"
              id="wholesalePrice"
              value={wholesalePrice}
              onChange={(e) => setwholesalePrice(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="consumerPrice">Consumer Price:</label>
            <input
              type="text"
              id="consumerPrice"
              value={consumerPrice}
              onChange={(e) => setconsumerPrice(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="MOQs">MOQs:</label>
            <input
              type="text"
              id="MOQs"
              value={MOQs}
              onChange={(e) => setMOQs(e.target.value)}
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="Colours Available">Colours Available:</label>
            <input
              type="text"
              id="Colours Available"
              value={availableColours}
              onChange={(e) => setAvailableColours(e.target.value)}
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="Sizes Available">Sizes Available:</label>
            <input
              type="text"
              id="Sizes Available"
              value={availableSizes}
              onChange={(e) => setAvailableSizes(e.target.value)}
              required
            />
          </div>
          <button type="submit" className="buttonSubmit">
            Submit
          </button>
        </form>
      </div>
    </div>
  );
};
export default AddProduct;
