import AddToCart from "../AddToCart/AddToCart";
import "reactjs-popup/dist/index.css";
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from "react-responsive-carousel";
import "./ProductCard.css";
import { useState, useEffect } from "react";

const ProductCard = (props) => {
  const [addToCart, setAddToCart] = useState(false);
  const [miniCart, setminiCart] = useState([]);
  const [ItemInCart, setItemInCart] = useState(false);

  useEffect(() => {
    console.log("My Cart", miniCart);
    props.AddToLargeCart(miniCart);
  }, [miniCart]);

  const AddToCartHandler = () => {
    setAddToCart(true);
    props.setSwipeEnabled(false);
  };

  const AddedToCart = (productID, selectedColors, quantities) => {
    setAddToCart(false);
    var Item = {
      productID: productID,
      selectedColors: selectedColors,
      quantities: quantities,
    };
    setminiCart([...miniCart, Item]);
    setItemInCart(true);
    props.setSwipeEnabled(true);
  };

  const BackToShopping = () => {
    setAddToCart(false);
    props.setSwipeEnabled(true);
  };
  return (
    <div className="ProductCard">
      <Carousel className="ProductImage" swipeable={false} swiping={false}>
        <img
          src={require(`./${props.product.productName}-1.jpg`)}
          alt="Product Image 1"
        />
        <img
          src={require(`./${props.product.productName}-2.jpg`)}
          alt="Product Image 2"
        />
      </Carousel>

      {!addToCart && (
        <div className={!addToCart ? "ProductVisible" : "ProductHidden"}>
          <div className="ProductInformation">
            <h1 className="h14">Product Information</h1>
            <div className="Info">
              <h5>Name: {props.product.productName}</h5>
              <h5>Code: {props.product.productID}</h5>
              <h5>Colours: R-G-Y</h5>
              <h5>Sizes: S-M-L-XL</h5>
              <h5>Material: {props.product.productMaterial}</h5>
              <h5>Wholesale Price: {props.product.productWholesalePrice}</h5>
              <h5>Consumer Price: {props.product.productConsumerPrice}</h5>
              <h5>MOQs: {props.product.productMOQs}</h5>
            </div>
            <br></br>
            {!ItemInCart && (
              <button className="AddToCart" onClick={AddToCartHandler}>
                Add to Cart
              </button>
            )}
            {ItemInCart && (
              <button className="AddToCart" disabled>
                Item already in cart
              </button>
            )}
            <br></br>
            {/*<button className="ViewCart" onClick={AddToCartHandler}>
              View Cart
            </button>*/}
          </div>
        </div>
      )}
      {addToCart && (
        <div className={addToCart ? "AddToCartCard" : "AddToCartCardHidden"}>
          <AddToCart
            BackToShopping={BackToShopping}
            availableColours={props.product.coloursAvailable}
            productID={props.product.productID}
            availableSizes={props.product.sizesAvailable}
            AddedToCart={AddedToCart}
            Cart={miniCart}
          ></AddToCart>
        </div>
      )}
    </div>
  );
};
export default ProductCard;
